.brick .io-area {
  cursor: crosshair; }

.brick rect {
  fill: #2b7a78;
  box-shadow: rgba(0, 0, 0, 0.5);
  stroke: #3aafa9;
  stroke-width: 1px; }

.brick text {
  fill: #def2f1;
  text-anchor: middle;
  dominant-baseline: middle; }
  .brick text.output {
    text-anchor: start;
    fill: #def2f1; }
