.context-menu {
  background: #17252a;
  font-size: 12px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
  min-width: 100px;
  margin: 2px; }
  .context-menu > ul {
    margin: 0;
    padding: 0;
    list-style: none;
    border: 1px solid #2b7a78; }
    .context-menu > ul > li {
      cursor: pointer;
      padding: 5px;
      border-bottom: 1px solid #2b7a78; }
      .context-menu > ul > li:last-child {
        border-bottom: none; }
      .context-menu > ul > li:hover {
        background: #2b7a78; }
