.project-list {
  padding: 16px; }
  .project-list .search-bar {
    margin-bottom: 16px;
    text-align: center;
    position: relative; }
    .project-list .search-bar input {
      border-radius: 30px;
      border: 1px solid #2b7a78;
      background: transparent;
      color: #def2f1;
      padding: 3px 30px 3px 15px;
      outline: none !important;
      transition: border-color .3s;
      width: 50%; }
      .project-list .search-bar input:focus {
        border-color: #3aafa9; }
    .project-list .search-bar .search-btn {
      position: relative;
      left: -24px;
      color: #2b7a78; }
      .project-list .search-bar .search-btn:hover {
        color: #3aafa9; }
  .project-list .project-list-item {
    width: 100%;
    height: 100%;
    border: 1px solid #feffff;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: color .3s, border-color .3s;
    color: #feffff; }
    .project-list .project-list-item .iconfont {
      font-size: 48px;
      transition: transform .3s; }
    .project-list .project-list-item .info {
      position: absolute;
      left: 0;
      top: calc(100% - 30px);
      height: 100%;
      width: 100%;
      font-size: 12px;
      line-height: 20px;
      padding: 5px;
      color: #def2f1;
      border: 1px solid transparent;
      background: rgba(0, 0, 0, 0.2);
      transition: top .3s; }
      .project-list .project-list-item .info h2 {
        font-size: 12px;
        color: inherit; }
      .project-list .project-list-item .info p {
        height: calc(100% - 26px);
        -webkit-mask: linear-gradient(to bottom, #fff, transparent); }
  .project-list .delete {
    display: block;
    position: absolute;
    right: 3px;
    top: 3px;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    color: #3aafa9;
    font-size: 12px;
    opacity: 0;
    transition: opacity .3s, color .3s; }
    .project-list .delete:hover {
      color: #def2f1; }
  .project-list .card {
    margin-bottom: 16px; }
    .project-list .card:hover .project-list-item {
      border-color: #3aafa9;
      color: #3aafa9; }
      .project-list .card:hover .project-list-item .iconfont {
        transform: scale(1.1); }
      .project-list .card:hover .project-list-item .info {
        top: 0; }
    .project-list .card:hover .delete {
      opacity: 1; }
